// -------------- THEME SWITCHER -------------- //
@mixin dark-appearance {
  filter: invert(1);
  img {
    filter: invert(1);
    &.ioda {
      filter: invert(0);
    }
  }
}
body[data-theme="dark"] {
  @include dark-appearance;
}
@media (prefers-color-scheme: dark) {
  body[data-theme="auto"] {
    @include dark-appearance;
  }
}
// -------------------------------------------- //
// bg color is also needed in html in order to
// block body's background propagation
// see: https://stackoverflow.com/a/61265706
html,
body {
  background: #FFFDF7;
}
html {
  height: 100%;
}
body {
  color: black;
  font-family: sans-serif;
  font-size: 14px;
  line-height: 1.4;
  margin: 0;
  min-height: 100%;
  overflow-wrap: break-word;
}
hr {
  margin: 2rem 0;
}
p {
  margin: 1rem 0;
}
li {
  margin: 0.4rem 0;
}
*:target {
  color: #D58936;
  border-radius: 5px;
  font-style: italic;
}
.w {
  max-width: 640px;
  margin: 0 auto;
  padding: 4rem 2rem
}
@media screen and (max-width: calc(640px + 2*2rem)) {
  .w {
      padding: 2rem;
  }
}

hr {
  text-align: center;
  border: 0
}

hr:before {
  content: "/////"
}

hr:after {
  content: attr(data-content) "/////"
}

table {
  width: 100%
}

table,
th,
td {
  border: thin solid black;
  border-collapse: collapse;
  padding: 0.4rem
}

code {
  color: black;
  background: #F7DE50;
  font-family: monospace
}

div.highlighter-rouge code {
  display: block;
  overflow-x: auto;
  white-space: pre-wrap;
  padding: 1rem;
  border-radius: 5px
}

blockquote {
  font-style: italic;
  border: thin solid black;
  padding: 1rem
}

blockquote p {
  margin: 0
}

img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}

.mode-wrapper {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  align-self: center;
}

.mode-wrapper #mode {
  background: black;
  font-size: 1.5rem;
  border: none;
  border-radius: 100%;
  height: 40px;
  width: 40px;
  padding: 10px;
  transition: background 300ms;
}

.mode-wrapper #mode:hover {
  background: #F7DE50;
}

.mode-wrapper span {
  color: black;
  text-align: center;
  display: inline-block;
  font-size: 10px;
  width: 40px;
}

a {
  color: #585123;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

footer {
  text-align: center;
}

.post-meta {
  text-align: right;
}